.App {
  text-align: center;
  background-image: url("./images//609316fc04f7da8f6a26f0d255a70c13.jpg");
}

.row-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: max-content;
}

.column-container {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 1s linear;
  }
}

.main-class {
  width: 100%;
}

.stickers-select {
  margin-right: 10px !important;
}

#stickers-select {
  color: white;
}

.send-button {
  width: max-content;
  margin-left: 20px;
}

.send-controls {
  border-radius: 3px;
  padding: 20px 40px;
  margin: 30px auto;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.2);
}

.imgUrlField {
  flex: 2;
}

#chatEnd {
  margin-bottom: -20px;
}

.nameField {
  flex: 2;
}

.messageField {
  flex: 5;
  min-width: 200px;
  width: 50vw;
}

.loading-label {
  color: blueviolet;
}

.textfield {
  margin-right: 30px !important;
}

.message-card {
  width: 90%;
  border: black;
  margin: 10px auto;
  display: flex;
  flex-direction: row;
  background-color: rgba(255, 255, 255, 0.445) !important;
}

.message-card-profile {
  width: 70px;
  max-width: 70px;
}

.userImage {
  padding: 0 !important;
  border: 1px solid;
  border-radius: 200px;
  border-color: rgba(0, 0, 0, 0.145);
  margin: 10px;
  max-width: max-content;
}

.user-image-button {
  padding: 0 !important;
  max-width: max-content;
}

.message-header-label {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 200px;
  margin-right: 10px !important;
  -webkit-line-clamp: 1;
  text-align: left;
}

.content-label {
  text-align: initial;
  overflow-wrap: anywhere;
  white-space: pre-line;
  display: flex;
  flex-direction: column;
  margin-top: 3px !important;
}

.content-label>p,
h1 {
  margin: 0 0 10px 0 !important;
}

.scrollable-chat {
  width: 100%;
  margin: 0 auto;
  overflow-y: scroll;
  height: 500px;
  /* Set a fixed height */
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  min-width: 90vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgba(32 95 42 / 86%);
}

.App-link {
  color: #61dafb;
}

.login-container {
  display: flex;
  flex-direction: column;
  border: 1px solid;
  border-color: gray;
  width: 400px !important;
  height: max-content;
  padding: 40px 20px !important;
  background-color: #ffffffbf;
}

.login-field {
  margin-bottom: 20px !important;
}

.room-select {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 500px;
  min-width: max-content;
}

.scroll-style::-webkit-scrollbar {
  width: 7px;
  /* ширина scrollbar */
}

.scroll-style::-webkit-scrollbar-track {
  background: rgba(142, 159, 255, 0.215);
  /* цвет дорожки */
}

.scroll-style::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.588);
  /* цвет плашки */
  border-radius: 20px;
  /* закругления плашки */
}

.chat-ui {
  width: 100%;
}

.room-button {
  margin-bottom: 20px !important;
  width: 100%;
  overflow: visible !important;
  padding: 3px 8px !important;
}

.room-card {
  background-color: rgba(255, 255, 255, 0.2) !important;
  width: 100%;
  overflow: visible !important;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


blockquote {
  padding: 0;
  margin: 0;
  line-height: 1.1;
  white-space: nowrap;
}

blockquote p {
  margin: 0 0 5px 0 !important;
  padding: 10px;
  background: #eeeeee43;
  border-radius: 5px;
}

blockquote p::before {
  content: '\201C';
}

blockquote p::after {
  content: '\201D';
}

.user-profile-modal {
  margin: auto;
  width: 250px;
  height: 400px;
  background-color: #0000208f;
  border-radius: 4px;
  border: 2px solid rgba(0, 0, 0, 0.47);
}

.user-profile-modal>.css-0 {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.modal-user-name {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  color: #ffffff !important;
  text-align: center;
}

.modal-pm-button {
  margin: auto;
  display: block;
  width: max-content;
  background-color: #ffca6c;
  border-radius: 3px;
  margin-top: 45%;
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  margin: 0 auto !important;
}

.modal-pm-button > a {
  color: black;
}

.user-modal-header {
  text-align: center;
  border-bottom: 1px #fff solid;
  margin-bottom: 10px !important;
}

.content-image {
  height: 100px;
  width: fit-content
}